import { Component, OnInit } from '@angular/core';
import { Meta } from '@angular/platform-browser';
import { Router } from '@angular/router';
import './imgcor.js';

declare var navSlide: any;

@Component({
  selector: 'app-image-courtsey',
  templateUrl: './image-courtsey.component.html',
  styleUrls: ['./image-courtsey.component.css']
})
export class ImageCourtseyComponent implements OnInit {

  constructor(
    private router: Router,
    private metaTagService: Meta
    ) { }

  ngOnInit(): void {
    this.metaTagService.addTags([
      { name: 'description', content: 'Angular SEO Integration, Music CRUD, Angular Universal' },
      { name: 'keywords', content: 'index, follow' },
      { name: 'author', content: 'Digamber Singh' },
      { charset: 'UTF-8' }
    ]);
  }

}
